/** CSS reset */

* {
  box-sizing: border-box;
}

html,
body,
div,
nav,
span,
textarea,
button,
ol,
ul,
article,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  font: inherit;
  font-family: 'Noto Sans KR', sans-serif;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
h5 {
  color: grey;
}
h6 {
  margin: 10px 0;
}
a {
  text-decoration: none;
}
html {
  scroll-behavior: smooth;
}
input:focus,
textarea:focus {
  outline: none;
}
:root {
  --main-color: #b9e0fb;
}

/** landpage CSS */
.landpage-container {
  max-width: 100%;
  width: 100vw;
  height: 95vh;
  position: relative;
}
.background {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #eff5f9;
}
.background-style {
  width: 30%;
  height: 100%;
  background-color: #b9e0fb;
  margin-left: auto;
}
.basket-img {
  position: absolute;
  width: 40%;
  height: 70%;
  right: 13%;
  top: 20%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('./basket.png');
}
.nav-container {
  position: absolute;
  width: 100%;
  height: 64px;
  background-color: rgba(245, 245, 245, 0.4);
  display: flex;
  justify-content: flex-end;
  padding-right: 48px;
  z-index: 1;
}
.nav-item {
  font-size: 20px;
  padding: 20px;
  margin: auto 0;
  font-weight: 700;
  color: #0074e8;
  cursor: pointer;
}
.quote-container {
  position: absolute;
  width: 100%;
  height: 50%;
  top: 50%;
  transform: translateY(-50%);
  padding: 15px 15%;
}
.main-quote {
  line-height: 3rem;
  margin-bottom: 1.5rem;
}

.register-container {
  max-width: 40vw;
  min-width: 300px;
  margin: auto;
}
#about {
  height: 100vh;
}
.about-header {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15%;
  font-size: 24px;
  letter-spacing: 0.4rem;
}
.about-content {
  height: 80%;
  background-size: cover;
  background-position: center;
  background-image: url('./aboutBack-min.png');
  padding: 4% 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-content #letter {
  line-height: 2.5rem;
  color: #808080;
}
.warning {
  border: 1px dotted #909090;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2%;
  padding: 0 2% 2%;
  line-height: 2.5rem;
}
.share-container {
  padding: 0 15vw;
}
.card-section {
  display: flex;
  flex-wrap: wrap;
}
.card-container {
  width: calc(25% - 44px);
  min-width: 120px;
  margin: 0 22px 56px;
}
.card-photo {
  border-radius: 12px;
  width: 100%;
}
.location {
  color: #777;
}

@media (max-width: 1100px) {
  .quote-container {
    background-color: rgba(250, 250, 250, 0.3);
  }
}

@media (max-width: 960px) {
  .basket-img {
    top: 40%;
    height: 40%;
  }
  .share-container {
    padding: 0 10vw;
  }
}

/** posting */
.post-container {
  padding: 5vh 15vw;
}
.upload {
  border: 1px solid #eee;
  border-width: 1px 0 0;
  display: flex;
  padding: 1em 0;
}
.input {
  border: 1px solid #eee;
  border-width: 1px 0 0;
  padding: 1.5em 0;
  width: 100%;
}
.post-content {
  border: 1px solid #eee;
  border-width: 1px 0 0;
  height: 40vh;
  width: 100%;
  padding: 1.5em 0;
  resize: none;
}
#input-file {
  display: none;
}
.file-upload {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 1px solid #eee;
  color: #777;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}

.post-button {
  width: 56px;
  height: 56px;
  font-size: 3em;
  border-radius: 50%;
  color: #fff;
  background-color: var(--main-color);
  position: fixed;
  right: 5%;
  top: 85%;
}

@media (max-width: 650px) {
  .share-container {
    padding: 0 3vw;
  }
  .card-container {
    width: 100%;
    display: flex;
    font-size: 12px;
    border-bottom: 1px solid #eee;
    padding: 1em 0;
    margin: 0;
  }
  .card-photo {
    width: 30%;
  }
  .card-content {
    padding: 0 5%;
  }

  .post-container {
    padding: 5vh 3vw;
  }
}
.sub-nav {
  display: flex;
  justify-content: space-between;
}
.done-button {
  padding: 0 2rem;
  color: #0074e8;
  background-color: transparent;
  font-size: 1.3em;
}
